.main-timeline {
  position: relative;
}

.timeline-header {
  text-align: center;
  color: #04724d;
}

.main-timeline:before {
  content: "";
  display: block;
  width: 1px;
  height: 100%;
  background: #36363659;
  margin: 0 auto;
  position: absolute;
  top: 32px;
  left: 0;
  right: 0;
}

.main-timeline .timeline {
  margin-bottom: 10px;
  position: relative;
}

.main-timeline .timeline:after {
  content: "";
  display: block;
  clear: both;
}

.main-timeline .date-content {
  width: 50%;
  float: left;
  margin-top: 22px;
  position: relative;
}

.main-timeline .date-content1 {
  width: 50%;
  float: right;
  margin-top: 22px;
  position: relative;
}

.main-timeline .date-content:before {
  content: "";
  width: 50px;
  height: 1px;
  background: #36363659;
  margin: auto 0;

  position: absolute;
  top: 0;
  right: 0px;
  bottom: 0;
}
.main-timeline .date-content1:before {
  content: "";
  width: 50px;
  height: 1px;
  background: #36363659;
  margin: auto 0;
  position: absolute;
  top: 0;
  left: 0px;
  bottom: 0;
}

.main-timeline .date-outer {
  width: 160px;
  font-size: 16px;
  text-align: center;
  margin: auto;
  z-index: 1;
}

.main-timeline .date-outer:before,
.main-timeline .date-outer:after {
  content: "";
  width: 125px;
  height: 50px;
  margin: 0 auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.main-timeline .timeline:hover .date-outer:before {
  left: 6px;
}

.main-timeline .timeline:hover .date-outer:after {
  left: -6px;
}

.main-timeline .date {
  width: 100%;
  margin: auto;
  position: absolute;
  top: 27%;
  left: 0;
}

.main-timeline .month {
  font-size: 18px;
  font-weight: 700;
}

.main-timeline .year {
  display: block;
  font-size: 30px;
  font-weight: 700;
  color: #232323;
  line-height: 36px;
}

.main-timeline .timeline-content {
  width: 50%;
  padding: 20px 0 20px 50px;
  float: right;
}
.main-timeline .timeline-content:before {
  content: "";
  width: 40px;
  height: 2px;
  background: #c6c6c6;
  margin: auto 0;
  position: absolute;
  top: 0;
  right: 0px;
  bottom: 0;
}

.main-timeline .title {
  font-size: 16px;
  color: #363636;
  opacity: 0.8;
  font-family: "Poppins";
}
.main-timeline .sub-title {
  font-size: 13px;
  color: #363636;
  opacity: 0.8;
  font-family: "Poppins";
}

.main-timeline .description {
  margin-bottom: 0;
}

.main-timeline .timeline:nth-child(2n) .date-content {
  float: right;
}

.main-timeline .timeline:nth-child(2n) .date-content:before {
  left: 10px;
}

.main-timeline .timeline:nth-child(2n) .timeline-content {
  padding: 20px 50px 20px 0;
  text-align: right;
}

@media only screen and (max-width: 991px) {
  .main-timeline .date-content {
    margin-top: 20px;
  }
  .main-timeline .date-content:before {
    width: 40px;
  }
  .main-timeline .date-content1 {
    margin-top: 20px;
  }
  .main-timeline .date-content1:before {
    width: 40px;
  }

  .main-timeline .timeline-content {
    padding: 10px 0 10px 30px;
  }
  .main-timeline .title {
    font-size: 17px;
  }
  .main-timeline .timeline:nth-child(2n) .timeline-content {
    padding: 10px 30px 10px 0;
  }
}

@media only screen and (max-width: 767px) {
  .main-timeline:before {
    margin: 0;
    left: 7px;
  }
  .main-timeline .timeline {
    margin-bottom: 20px;
  }
  .main-timeline .timeline:last-child {
    margin-bottom: 0;
  }
  .main-timeline .icon {
    margin: auto 0;
  }
  .main-timeline .date-content {
    width: 95%;
    float: right;
    margin-top: 10px;
  }
  .main-timeline .date-content1 {
    width: 95%;
    float: right;
    margin-top: 10px;
  }

  .main-timeline .date-content:before {
    display: none;
  }
  .main-timeline .date-content1:before {
    display: none;
  }
  .main-timeline .date-outer {
    width: 110px;
  }
  .main-timeline .date-outer:before,
  .main-timeline .date-outer:after {
    width: 110px;
  }
  .main-timeline .date {
    top: 30%;
  }
  .main-timeline .year {
    font-size: 24px;
  }
  .main-timeline .timeline-content,
  .main-timeline .timeline:nth-child(2n) .timeline-content {
    width: 95%;
    text-align: center;
    padding: 10px 0;
  }
  .main-timeline .title {
    margin-bottom: 10px;
  }
}
