.cardContent {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

}

.cardTitle {
  /* height: 25px;
  width: 17px; */
  text-align: right;
  font-weight: 600;
  color: #c69e5d;
  opacity: 1;
  /* margin: 0px 15px; */
}

.cardContentText {
  /* height: 29px; */
  text-align: right;
  font-size: 16px;
  color: #333333;
  opacity: 1;
  margin: 2px 0px;
  /* padding-top: 20px; */
}

.com-card {
  width: 217px;
  height: 116px;
  background: #ffffff 0% 0% no-repeat padding-box;
  border-radius: 3px;
  opacity: 1;
  padding: 20px;
}

@media (max-width: 1920px) {
  .com-card {
    width: 264px;
    padding: 16px;
  }
}

@media (max-width: 1366px) {
  .com-card {
    width: 264px;
    padding: 12px;
  }
}

@media (max-width: 1280px) {
  .com-card {
    width: 264px;
    padding: 10px;
  }
}

@media (max-width: 1024px) {
  .com-card {
    width: 252px;
    padding: 20px;
  }
}

@media (max-width: 768px) {
  .com-card {
    width: 190px;
    padding: 6px;
    height: 100px;
  }
  .com-card img {
    width: 50px;
    height: 50px;
  }
}
